html,
body {
  direction: rtl;
}

img {
  opacity: 1 !important;
}

a {
  transition: all 150ms linear;
}

.post-card .post-card-image {
  border-radius: 20px;
}
.post-card .post-card-title {
  direction: rtl;
  font-size: 2.5rem;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  color: white;
}
.post-card .post-card-excerpt {
  direction: rtl;
  text-align: justify;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.post-card .post-card-excerpt p {
  font-size: 1.5rem;
}
.post-card .post-card-meta {
  direction: ltr;
  align-self: flex-start;
  color: var(--color-secondary-text) !important;
}
.post-card .post-card-primary-tag {
  color: var(--color-secondary-text) !important;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}

.post .post-card-image {
  opacity: 0.9 !important;
  transition: opacity 200ms linear;
}
.post .post-card-image:hover {
  opacity: 1 !important;
}
.post .author-name {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.post .article-header .article-title {
  direction: rtl;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  font-size: clamp(3rem, 5vw, 5rem);
}
.post .article-header .article-excerpt {
  direction: rtl;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.post .article-header .article-byline .article-byline-meta .author-name {
  margin: 0 6px 0 0;
}
.post .article-header .article-byline .article-byline-meta .byline-meta-content {
  margin: 6px 6px 0 0;
  color: var(--color-secondary-text);
}
.post .article-header .article-image img {
  border-radius: 50px;
}
.post .article-tag {
  margin-bottom: 20px;
}
.post .article-tag a {
  padding: 3px 6px 0px 6px;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  color: var(--color-secondary-text) !important;
}
.post .article-tag .tag-list {
  color: var(--color-secondary-text) !important;
}
.post .article-tag .post-card-featured {
  padding-left: 0;
  padding-right: 18px;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  word-break: keep-all;
}
.post .article-tag .post-card-featured svg {
  left: unset !important;
  right: 0;
}
.post .post-full-comments {
  margin: 2em 3em 0 3em;
}

.gh-content p {
  color: white;
  direction: rtl;
  text-align: justify;
  text-align-last: right;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  font-size: 2rem;
  font-weight: 300;
}
.gh-content p a {
  text-decoration: none !important;
  transition: color 250ms;
  color: #00726a !important;
}
.gh-content p a:hover {
  color: #20c5ba !important;
}
.gh-content p strong {
  font-weight: 500;
  color: #91deda;
}
.gh-content p code {
  word-break: break-word;
  padding: 3px 5px 3px 5px;
  unicode-bidi: plaintext;
}
.gh-content ul,
.gh-content ol {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.gh-content ul li,
.gh-content ol li {
  font-size: 16px;
}
.gh-content ul li::marker,
.gh-content ol li::marker {
  color: #20c5ba;
}
.gh-content pre {
  direction: ltr !important;
  border-radius: 10px;
  background: #292d3e;
  border: none;
}
.gh-content h2,
.gh-content h3,
.gh-content h4 {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  color: #20c5ba !important;
}
.gh-content figure img {
  border-radius: 10px;
}
.gh-content figure .kg-bookmark-container {
  direction: ltr;
  border-radius: 10px;
}
.gh-content figure .kg-bookmark-container .kg-bookmark-title,
.gh-content figure .kg-bookmark-container .kg-bookmark-description {
  direction: rtl;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.gh-content blockquote {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
  font-size: 16px !important;
  color: #92a3ab;
  border-right: 3px solid #20c5ba;
  border-left: 3px solid #20c5ba;
  padding: 0 10px !important;
}
.gh-content img {
  max-width: 100%;
  border-radius: 10px;
}
.gh-content video {
  max-width: 100%;
}

.nav li a {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}

.error-content .error-message .error-link {
  color: var(--color-secondary-text);
  direction: ltr;
}

.site-footer {
  padding-bottom: 40px;
  padding-top: 40px;
}

.gh-head-brand .gh-search {
  margin-right: 0;
  margin-left: 8px;
}

.has-cover .site-header-content {
  min-height: 506px;
}

.site-header-cover {
  opacity: 0.75 !important;
}

.kg-bookmark-container {
  border-radius: 15px !important;
}
.kg-bookmark-container img {
  border-radius: 0 15px 15px 0 !important;
}

.kg-button-card a {
  background-color: #20c5ba !important;
  border-radius: 10px !important;
}

.kg-callout-card {
  border-radius: 15px !important;
}
.kg-callout-card div.kg-callout-emoji {
  padding-right: 0 !important;
  padding-left: 0.8em !important;
}
.kg-callout-card .kg-callout-text {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}

.kg-toggle-card {
  border-radius: 15px !important;
}
.kg-toggle-card p {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}

.kg-product-card-container {
  border-radius: 15px !important;
}
.kg-product-card-container img {
  border-radius: 15px !important;
}
.kg-product-card-container .kg-product-card-button {
  background-color: #20c5ba !important;
  border-radius: 10px !important;
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}
.kg-product-card-container h4.kg-product-card-title,
.kg-product-card-container .kg-product-card-description p {
  font-family: Vazirmatn RD UI FD, sans-serif !important;
}

.yektanet {
  margin: 2em 3em 0 3em;
}

@media (max-width: 767px) {
  #gh-head .gh-head-brand .gh-search {
    margin-left: 0;
    margin-right: -6px;
  }
  .yektanet,
  .post-full-comments {
    margin: 1em 2em 0 2em;
  }
}
@media (max-width: 767px) {
  .nikas-comment > .nikas-avatar {
    margin: 3px 0 0 27px;
  }
}